.form {

    display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 50px;
        padding-left: 40px;
        padding-right: 40px;
        padding-bottom: 50px;
        background-color: rgb(184, 213, 246);
        width: 500px;
}

.label {
display: block;
    margin-bottom: 10px;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
}

.label:not(:first-child) {
    margin-top: 20px;
}

.input {
    width: 400px;
        padding: 10px;
        margin-bottom: 40px;
        font-size: 24px;
}

.title {
    font-size: 30px;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 50px;
}